import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';
import attachmentsDirective from 'shared/attachments/attachments.directive';

export default angular.module('deskworks.WelcomeLetterCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  attachmentsDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('WelcomeLetterCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.WELCOME_LETTER.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;
    $ctrl.attachments = $ctrl.tpl.attachments || [];
    delete $ctrl.tpl.attachments; // delete to exclude from POST

    $ctrl.shortCodes = [
      { id: '{{member-name}}', _name: 'SH_CODES.FULL_NAME' },
      { id: '{{first-name}}', _name: 'SH_CODES.FIRST_NAME' },
      { id: '{{last-name}}', _name: 'SH_CODES.LAST_NAME' },
      { id: '{{company}}', _name: 'SH_CODES.COMPANY' },
      { id: '{{occupation}}', _name: 'SH_CODES.OCCUPATION' },
      { id: '{{email}}', _name: 'SH_CODES.EMAIL' },
      { id: '{{phone}}', _name: 'SH_CODES.PHONE' },
      { id: '{{login}}', _name: 'SH_CODES.LOGIN' },
      { id: '{{lead-source}}', _name: 'SH_CODES.LEAD_SOURCE' },
      { id: '{{start-date}}', _name: 'SH_CODES.START_DATE' },
      { id: '{{renewal-date}}', _name: 'SH_CODES.RENEWAL_DATE' },
      { id: '{{renewal-term}}', _name: 'SH_CODES.RENEWAL_TERM' },
      { id: '{{product}}', _name: 'SH_CODES.PRODUCT' },
      { id: '{{price}}', _name: 'SH_CODES.PRICE' },
      { id: '{{discount}}', _name: 'SH_CODES.DISCOUNT' },
      { id: '{{net}}', _name: 'SH_CODES.NET' },
      { id: '{{company-name}}', _name: 'SH_CODES.COMPANY_NAME' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#welcomeLetter')) return;

      return Notifications.saveWelcomeLetter($ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save welcome letter template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };

    /**
     * Attach/detach file
     */
    $ctrl.attacher = (att) => Notifications.attachWelcomeLetter(att);
    $ctrl.detacher = (att) => Notifications.detachWelcomeLetter(att.id);
  });
