import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';
import attachmentsDirective from 'shared/attachments/attachments.directive';

export default angular.module('deskworks.ReservationConfirmationEmailCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  attachmentsDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('ReservationConfirmationEmailCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.RESERVATION_CONFIRMATION.PAGE_TITLE'));

    $ctrl.tabs = [
      {
        id: 'confirm',
        title: $translate.instant('NOTIFICATIONS.RESERVATION_CONFIRMATION.RESERVATION'),
        tpl: tpl.confirm,
      },
      {
        id: 'cancel',
        title: $translate.instant('NOTIFICATIONS.RESERVATION_CONFIRMATION.CANCELLATION'),
        tpl: tpl.cancel,
      },
      {
        id: 'repeat',
        title: $translate.instant('NOTIFICATIONS.RESERVATION_CONFIRMATION.REPEATING'),
        tpl: tpl.repeat,
      },
    ];

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;
    $ctrl.attachments = $ctrl.tpl.attachments || [];
    delete $ctrl.tpl.attachments; // delete to exclude from POST

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{center-phone}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_PHONE' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{reservation-unit}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_UNIT' },
      { id: '{{repeat-frequency}}', _name: 'NOTIFICATIONS.SH_CODES.REPEAT_FREQUENCY' },
      { id: '{{access-code}}', _name: 'NOTIFICATIONS.SH_CODES.ACCESS_CODE' },
      { id: '{{reservation-start-time}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_START_TIME' },
      { id: '{{reservation-end-time}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_END_TIME' },
      { id: '{{reservation-date}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_DATE' },
      { id: '{{reservation-start-date}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_START_DATE' },
      { id: '{{reservation-end-date}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_END_DATE' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#reservationConfirmationEmail')) return;

      return Notifications.saveReservationConfirmation($ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save reservation confirmation template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };

    /**
     * Attach/detach file
     */
    $ctrl.attacher = (att) => Notifications.attachReservationConfirmation(att);
    $ctrl.detacher = (att) => Notifications.detachReservationConfirmation(att.id);
  });
