import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.ExpiredCardEmailCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('ExpiredCardEmailCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.EXPIRED_CARD.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{center-phone}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_PHONE' },
      { id: '{{card-number}}', _name: 'NOTIFICATIONS.SH_CODES.CARD_NUMBER' },
      { id: '{{website-url}}', _name: 'NOTIFICATIONS.SH_CODES.WEBSITE_URL' },
      { id: '{{card-expiry}}', _name: 'NOTIFICATIONS.SH_CODES.CARD_EXPIRY' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#expiredCardEmail')) return;

      return Notifications.saveExpiredCardEmail($ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save expired card email template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
