import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';
import attachmentsDirective from 'shared/attachments/attachments.directive';

export default angular.module('deskworks.ReservationReminderEmailCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  attachmentsDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('ReservationReminderEmailCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.RESERVATION_REMINDER.PAGE_TITLE'));

    $ctrl.toolbar = taToolbarEmail;
    $ctrl.reminders = tpl.reminders || [];
    $ctrl.attachments = tpl.attachments || [];

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{reservation-unit}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_UNIT' },
      { id: '{{reservation-date}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_DATE' },
      { id: '{{reservation-start-time}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_START_TIME' },
      { id: '{{reservation-end-time}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_END_TIME' },
      { id: '{{access-code}}', _name: 'NOTIFICATIONS.SH_CODES.ACCESS_CODE' },
    ];

    /**
     * Add reminder
     */
    $ctrl.add = () => {
      const reminder = {
        subject: 'Reservation Reminder',
        content: `<p>Dear {{member-name}},</p>
<br>
<p>You have a reservation tomorrow at {{reservation-start-time}} at {{center-name}} for {{reservation-unit}}.</p>
<p>Let us know if you need anything. We look forward to seeing you!</p>
<br>
<p>Sincerely,<br>
{{center-name}} Team</p>
`,
      };
      if ($ctrl.reminders.length) {
        reminder.subject = $ctrl.reminders[0].subject;
        reminder.content = $ctrl.reminders[0].content;
      } else {
        reminder.hoursBefore = 23;
      }
      $ctrl.reminders.push(reminder);
      $ctrl.form.$setDirty();
    };

    /**
     * Delete reminder
     */
    $ctrl.delete = (index) => {
      $ctrl.reminders.splice(index, 1);
      $ctrl.form.$setDirty();
    }

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#reservationReminderEmail')) return;

      return Notifications.saveEmailTemplate('reservation-reminder', { reminders: $ctrl.reminders })
        .catch(err => dwAlerts.error(err, 'Failed to save reservation reminder template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };

    /**
     * Attach/detach file
     */
    $ctrl.attacher = (att) => Notifications.attachEmailTemplate('reservation-reminder', att);
    $ctrl.detacher = (att) => Notifications.detachEmailTemplate('reservation-reminder', att.id);
  });
