import './desk-tablet-template.scss';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.DeskTabletTemplateCtrl', [
  'deskworks.notifications.service',
  taToolbarEmail.name,
])
  .controller('DeskTabletTemplateCtrl', function($scope, $translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.DESK_TABLET_TEMPLATE.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{access-code}}', _name: 'NOTIFICATIONS.SH_CODES.ACCESS_CODE_OF_THE_DAY' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = function() {
      if (helper.pointFirstInvalid('#deskTabletTemplate')) return;

      return Notifications.saveDeskTabletTemplate(state.getCurrentCenterId(), $ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save dest tablet template.'))
        .then(() => { $ctrl.form.$setPristine(); });
    };

    $ctrl.toAccessCode = () => state.setPath('/access-code-of-the-day');
  });
