import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.RefundNoticeCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('RefundNoticeCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.REFUND_NOTICE.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{center-phone}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_PHONE' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{last-name}}', _name: 'NOTIFICATIONS.SH_CODES.LAST_NAME' },
      { id: '{{amount}}', _name: 'SH_CODES.REFUND_AMOUNT' },
      { id: '{{order-id}}', _name: 'SH_CODES.ORDER_NO' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#refundNotice')) return;

      return Notifications.saveEmailTemplate('refund-notice', $ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save refund notice settings.'))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
