import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.MessageBoardNoticesTplCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('MessageBoardNoticesTplCtrl', function($q, $translate, dwAlerts, state, helper, tpls, Notifications, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.MESSAGE_BOARD.PAGE_TITLE'));

    $ctrl.tpls = tpls;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{member-name}}', _name: 'SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{messages}}', _name: 'SH_CODES.MESSAGES' },
      { id: '{{center-name}}', _name: 'SH_CODES.CENTER_NAME' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = function() {
      if (helper.pointFirstInvalid('#messageBoardNoticesTpl')) return;

      return $q.all($ctrl.tpls.map(tpl => Notifications.saveMessageBoardEmailTemplate(tpl.$$id, tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save message board email template.'))))
        .then(() => { $ctrl.form.$setPristine(); });
    };
  });
