import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.BulkInvoicesTplCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('BulkInvoicesTplCtrl', function($translate, dwAlerts, state, helper, tpl, Notifications, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.BULK_INVOICES.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{card-number}}', _name: 'NOTIFICATIONS.SH_CODES.CARD_NUMBER' },
      { id: '{{center-phone}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_PHONE' },
      { id: '{{two-business-days}}', _name: 'NOTIFICATIONS.SH_CODES.TWO_BUSINESS_DAYS' },
      { id: '{{website-url}}', _name: 'NOTIFICATIONS.SH_CODES.WEBSITE_URL' },
      { id: '{{year}}', _name: 'NOTIFICATIONS.SH_CODES.YEAR' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{month}}', _name: 'NOTIFICATIONS.SH_CODES.MONTH' },
      { id: '{{invoice}}', _name: 'NOTIFICATIONS.SH_CODES.INVOICE' },
      { id: '{{last-month}}', _name: 'NOTIFICATIONS.SH_CODES.LAST_MONTH' },
      { id: '{{pdf-link}}', _name: 'NOTIFICATIONS.SH_CODES.PDF_LINK' },
      { id: '{{last-month-year}}', _name: 'NOTIFICATIONS.SH_CODES.LAST_MONTH_YEAR' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#bulkInvoicesTpl')) return;

      return Notifications.saveBulkInvoices($ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save bulk invoices template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
