import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.RenewalNoticeEmailCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])

  .controller('RenewalNoticeEmailCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.RENEWAL_NOTICE.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{last-name}}', _name: 'NOTIFICATIONS.SH_CODES.LAST_NAME' },
      { id: '{{renewal-notice-date}}', _name: 'NOTIFICATIONS.SH_CODES.RENEWAL_NOTICE_DATE' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#renewalNoticeEmail')) return;

      return Notifications.saveEmailTemplate('renewal-notice', $ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save renewal notice email template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
