import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';

export default angular.module('deskworks.ReceiptPageTplCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  'deskworks.notifications.service',
])
  .controller('ReceiptPageTplCtrl', function($q, $translate, dwAlerts, state, helper, tpl, Notifications) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.RECEIPT_PAGE.PAGE_TITLE'));

    $ctrl.tpl = tpl;

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#receiptPageTpl')) return;

      return Notifications.saveEmailTemplate('receipt-page', tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save receipt page template.'))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
