import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.ReservationFollowUpSurveyCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])
  .controller('ReservationFollowUpSurveyCtrl', function($translate, dwAlerts, state, helper, Notifications, tpl, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.RESERVATION_FOLLOW_UP_SURVEY.PAGE_TITLE'));

    $ctrl.tpl = tpl;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'SH_CODES.CENTER_NAME' },
      { id: '{{center-phone}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_PHONE' },
      { id: '{{center-address}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_ADDRESS' },
      { id: '{{user-name}}', _name: 'SH_CODES.USER_NAME' },
      { id: '{{first-name}}', _name: 'SH_CODES.USER_FIRST_NAME' },
      { id: '{{last-name}}', _name: 'SH_CODES.USER_LAST_NAME' },
      { id: '{{reservation-unit}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_UNIT' },
      { id: '{{reservation-date}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_DATE' },
      { id: '{{reservation-start-time}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_START_TIME' },
      { id: '{{reservation-end-time}}', _name: 'NOTIFICATIONS.SH_CODES.RESERVATION_END_TIME' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#reservationFollowUpSurvey')) return;

      return Notifications.save('reservation-follow-up-survey', $ctrl.tpl)
        .catch(err => dwAlerts.error(err, 'Failed to save reservation follow up survey settings.'))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
