import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import shortCodesDirective from 'shared/short-codes/short-codes.directive';
import taToolbarEmail from 'shared/ff-richtext/ta-toolbar-email.service';

export default angular.module('deskworks.ReceiptEmailTplCtrl', [
  ffRichtextDirective.name,
  shortCodesDirective.name,
  taToolbarEmail.name,
  'deskworks.notifications.service',
])
  .controller('ReceiptEmailTplCtrl', function($q, $translate, dwAlerts, state, helper, tpls, Notifications, taToolbarEmail) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('NOTIFICATIONS.RECEIPT.PAGE_TITLE'));

    $ctrl.tpls = tpls;
    $ctrl.toolbar = taToolbarEmail;

    $ctrl.shortCodes = [
      { id: '{{center-name}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_NAME' },
      { id: '{{member-name}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_NAME' },
      { id: '{{first-name}}', _name: 'NOTIFICATIONS.SH_CODES.FIRST_NAME' },
      { id: '{{center-street}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_STREET' },
      { id: '{{member-company}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_COMPANY' },
      { id: '{{center-city}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_CITY' },
      { id: '{{member-street}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_STREET' },
      { id: '{{center-state}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_STATE' },
      { id: '{{member-city}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_CITY' },
      { id: '{{center-zip}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_ZIP' },
      { id: '{{member-state}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_STATE' },
      { id: '{{center-email}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_EMAIL' },
      { id: '{{member-zip}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_ZIP' },
      { id: '{{center-phone}}', _name: 'NOTIFICATIONS.SH_CODES.CENTER_PHONE' },
      { id: '{{member-country}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_COUNTRY' },
      { id: '{{website-url}}', _name: 'NOTIFICATIONS.SH_CODES.WEBSITE_URL' },
      { id: '{{member-email}}', _name: 'NOTIFICATIONS.SH_CODES.MEMBER_EMAIL' },
      { id: '{{order-id}}', _name: 'NOTIFICATIONS.SH_CODES.ORDER_ID' },
      { id: '{{user-id}}', _name: 'NOTIFICATIONS.SH_CODES.USER_ID' },
      { id: '{{transaction-id}}', _name: 'NOTIFICATIONS.SH_CODES.TRANSACTION_ID' },
      { id: '{{invoice}}', _name: 'NOTIFICATIONS.SH_CODES.INVOICE' },
    ];

    /**
     * Save settings
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#receiptEmailTpl')) return;

      return $q.all($ctrl.tpls.map(tpl =>
        Notifications.saveEmailTemplate(tpl.$$id, tpl)
          .catch(err => dwAlerts.error(err, 'Failed to save receipt email template.'))))
        .then(() => { $ctrl.form.$setPristine() });
    };
  });
